import test from 'ava'
var unity = require('@locational/unity')
const store = new unity.Store()

store.registerValue('number_value',9)
store.registerValue('table',[
    {
        name:'Sihle',
        surname:'Mbhamali',
        age:9
    },
    {
        name:'John',
        surname:'Smithi',
        age:10
    }
])


const config_a = {
    input: { value: [1, 2, 3] },
    pipeline: [
        {
            type: 'where',
            options: {
                expression: {
                    gt: [
                        { dep: '$element' },
                        { dep: 'number_value' }
                    ]
                }
            }
        }
    ]
};




test('Store Register and lookup Value',t => {
  let actual = 9;
  let expected = store.lookupValue('number_value')
  t.deepEqual(actual, expected)  
})

test('Store lookup table',t => {
    let actual = [
        {
            name:'Sihle',
            surname:'Mbhamali',
            age:9
        },
        {
            name:'John',
            surname:'Smithi',
            age:10
        }
    ]
    let expected = store.lookupValue('table')
    t.deepEqual(actual, expected)  
})

test('Store Config', t =>{
    store.registerConfig('config_a',config_a)
    t.throws(() => { store.registerConfig('u1', config_a); });
})